import React, { useState, useEffect } from 'react'
import CountUp from 'react-countup'
import styled, { keyframes } from "styled-components";
import './covid.css'
// import './tooltip.css'
import coronaIcon from "../imgs/coronaIcon.svg"
import TabulkaMesta from "./TabulkaMesta"
import DougnatChart from "./DougnatChart"

function CovidSK() {
    const [dataSvk, setDataSvk] = useState()
    const svk = 'https://mapa.covid.chat/map_data'
    // const [tabulkaMestaData, setTabulkaMestaData] = useState([])

    const [state, setState] = useState({
        dataSvk: dataSvk,
        direction: {
            price_usd: 'asc',
        }
    })

    useEffect(() => {
        LoadData()
    }, [])

    const negatTestyMoje = [83, 102, 50, 42, 120, 129, 118, 197, 228]

    const LoadData = async () => {
        const response = await fetch(svk)
        const data = await response.json()
        setDataSvk([data])
        // console.log(data.map)
        setState({
            dataSvk: data.map.sort((a, b) => (
                state.direction['cases'] === 'asc'
                    ? parseFloat(a['cases']) - parseFloat(b['cases'])
                    : parseFloat(b['cases']) - parseFloat(a['cases'])
            )),
            direction: {
                ['cases']: state.direction['cases'] === 'asc'
                    ? 'desc'
                    : 'asc'
            }
        })
    }

    function handleData() {
        let a = dataSvk[0].chart[Object.keys(dataSvk[0].tested_chart).length].recovered
        let b = dataSvk[0].chart[Object.keys(dataSvk[0].tested_chart).length - 1].recovered
        let c = a - b
        let newRecovered = 0
        if (c > 0) {
            newRecovered = a - b
        }

        a = dataSvk[0].chart[Object.keys(dataSvk[0].tested_chart).length].deaths
        b = dataSvk[0].chart[Object.keys(dataSvk[0].tested_chart).length - 1].deaths
        c = a - b
        let newDeaths = 0
        if (c > 0) {
            newDeaths = a - b
        }


        return (
            <div>
                <div className="cardsDiv">
                    <div className="cards cardsSVK">
                        <div className="topCard">
                            <div className="obrDiv">
                                <div className="circle">
                                    <img alt="" src={coronaIcon} />
                                </div>
                            </div>
                            <div className="textCard"><b>Slovenské údaje</b></div>
                        </div>
                        <div className="mainTextSK">
                            Počet testovaných: {dataSvk[0].tested}<br />
                                Počet negatívnych testov: {dataSvk[0].tested - dataSvk[0].infected}<br />
                                Počet pozitívnych testov: {dataSvk[0].infected}<br />
                                Počet vyliečených: {dataSvk[0].recovered}<br />
                            {dataSvk[0].deaths > 0 ? <>Počet úmrtií: {dataSvk[0].deaths}<br /></>
                                : null}
                        </div>
                    </div>


                    <div className="cards cardsSVK">
                        <div className="topCard">
                            <div className="textCard"><b>Údaje za posledný deň<span style={{ color: 'grey', fontSize: '11px' }}> ({dataSvk[0].tested_chart[Object.keys(dataSvk[0].tested_chart).length - 1].day})</span></b></div>
                        </div>
                        <div className="mainTextSK">
                            Počet nových testovaných: <span>{dataSvk[0].tested_chart[Object.keys(dataSvk[0].tested_chart).length - 1].tested}</span><br />
                                Počet negatívnych testov: <span>{dataSvk[0].tested_chart[Object.keys(dataSvk[0].tested_chart).length - 1].tested - dataSvk[0].tested_chart[Object.keys(dataSvk[0].tested_chart).length - 1].infected}</span><br />
                                Počet pozitívnych testov: <span>+{dataSvk[0].tested_chart[Object.keys(dataSvk[0].tested_chart).length - 1].infected}</span>
                            {
                                newRecovered > 0 ?
                                    <> <br />Počet nových vyliečených: {newRecovered}<br /></>
                                    : null
                            }
                            {
                                newDeaths > 0 ?
                                    <> Počet nových úmrtí: {newDeaths}<br /></>
                                    : null
                            }
                        </div>
                    </div>


                </div>




            </div>
        )
    }
    const Heading = keyframes`
    0%{
        height: 0%;
      }
      100%{
        height: 100%;
      }
    `;

    const HomeHeader = styled.div`
    height: 100%;
    animation: ${Heading} 2s;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    `;

    function handleGraph1() {
        const MaxInfected = 100 / dataSvk[0].chart[Object.keys(dataSvk[0].chart).length - 1].infected
        return (
            <div className="outsideDivGraf">
                <div className="svkGraf">

                    {/* <div className="tooltip">Hover over me
                        <div className="tooltiptext">Tooltip text</div>
                        </div> */}


                    {
                        dataSvk[0].chart.map((day1, index) =>
                            <HomeHeader key={index}>
                                <div className="stlpcePrvehoGrafu tooltip" style={{ height: `calc(${MaxInfected * day1.infected}% )` }} >
                                    <CountUp className="CountPrvehoGrafu" end={parseInt(day1.infected)} />
                                    {dataSvk[0].tested_chart[index - 1]
                                        ? <> <CountUp className="countupFirstRed" prefix="+" end={parseInt(dataSvk[0].tested_chart[index - 1].infected)} />
                                            {/* <div className="tooltiptext">Počet nakazených: {day1.infected} <br />Počet nových nakazených: {dataSvk[0].tested_chart[index - 1].infected}</div> */}
                                        </>
                                        : <><CountUp className="countupFirstRed" prefix="+" end={1} />
                                        </>}
                                    <div className="spanDate">{day1.date}</div>
                                </div>
                            </HomeHeader>
                        )
                    }
                </div>
                <div className="medzeraPodDatumomGraf1"></div>
            </div>
        )
    }

    function handleGraph2() {
        let MaxDaily = 0
        if (dataSvk) {
            dataSvk[0].tested_chart.map(pom1 => {
                if (pom1.tested > MaxDaily) {
                    MaxDaily = pom1.tested - pom1.infected
                }
            })
            MaxDaily = MaxDaily / 100
        }
        return (
            <div className="outsideDivGraf">
                <div className="svkGraf">
                    <div className="testDElete"  ></div>

                    {<HomeHeader >  {/* prvy stlpec pre druhy graf */}
                        <div className="PozitivneTestyDruhyGraf" style={{ height: ` 3px` }} >

                            <div className="spanDate">{"06-03-2020"}</div>
                        </div>
                        <div className="dayX2 dayX2pom" style={{ height: `calc(${306 / MaxDaily}% + 3px)` }} >
                            <CountUp className="countupX2 CountDruhehoGrafu" end={306} />
                            <CountUp className="countup CountDruhehoGrafu nakazeniZaDen" prefix="+" end={1} />
                        </div>
                    </HomeHeader>}

                    {dataSvk[0].tested_chart.map((day1, index) =>
                        <HomeHeader key={index}>
                            {
                                <>
                                    {day1.infected === 0
                                        ?
                                        <div className="PozitivneTestyDruhyGraf"  >
                                            {/* <CountUp className="countup2 CountDruhehoGrafu " end={day1.infected} /> */}
                                            <div className="spanDate">{day1.date}</div>
                                        </div>
                                        :
                                        <div className="PozitivneTestyDruhyGraf" style={{ height: `calc(${(day1.infected) / MaxDaily}% + 3px)` }} >
                                            <div className="spanDate">{day1.date}</div>
                                        </div>
                                    }
                                    {
                                        negatTestyMoje[index]
                                            ?
                                            <div className="dayX2" style={{ height: `calc(${negatTestyMoje[index] / MaxDaily}% + 3px)` }} >
                                                {(negatTestyMoje[index] > 99) ? <>
                                                    <CountUp className="countupX2 CountDruhehoGrafu" end={negatTestyMoje[index]} style={{ marginLeft: '-2px' }} />
                                                    <CountUp className="countup2 CountDruhehoGrafu nakazeniZaDen" prefix="+" end={day1.infected} />
                                                </>
                                                    : <>
                                                        <CountUp className="countupX2 CountDruhehoGrafu" end={negatTestyMoje[index]} />
                                                        <CountUp className="countup2 CountDruhehoGrafu nakazeniZaDen" prefix="+" end={day1.infected} />
                                                    </>
                                                }
                                            </div>
                                            : <div className="dayX2" style={{ height: `calc(${day1.tested / MaxDaily}% + 3px)` }} >
                                                <CountUp className="countupX2 CountDruhehoGrafu" end={day1.tested - day1.infected} />
                                                <CountUp className="countup2 CountDruhehoGrafu nakazeniZaDen" prefix="+" end={day1.infected} />
                                            </div>
                                    }
                                </>
                            }
                        </HomeHeader>
                    )}
                </div>
                <div className="vysvetlivkyPodGrafom">
                    <div><div className="blue"></div>Počet negatívnych testov</div>
                    <div><div className="red"></div>Počet pozitívnych testov</div>
                </div>
            </div>
        )
    }


    function sortBy(key) {
        setState({
            dataSvk: dataSvk[0].map.sort((a, b) => (

                state.direction[key] === 'asc'
                    ? parseFloat(a[key]) - parseFloat(b[key])
                    : <>{parseFloat(b[key]) - parseFloat(a[key])} </>
            )),
            direction: {
                [key]: state.direction[key] === 'asc'
                    ? 'desc'
                    : 'asc'
            }
        })
    }
    return (
        <div className="covidApp">
            {
                dataSvk
                    ? <>
                        {handleData()}
                        <TabulkaMesta tabulkaData={dataSvk[0].map} sortBy={sortBy} />

                        {/* <div className="tooltip">Hover over me
                        <div className="tooltiptext">Tooltip text</div>
                        </div> */}
                        {/* <DougnatChart /> */}

                        {handleGraph1()}
                        {handleGraph2()}
                    </>
                    : ''


            }
        </div >
    )
}

export default CovidSK
