import React from 'react'
import { Doughnut } from 'react-chartjs-2';
import './csspreSvk.css'
function DougnatChart() {
    // const data = {
    //     datasets: [{
    //         data: [10, 20, 30]
    //     }],

    //     // These labels appear in the legend and in the tooltips when hovering different arcs
    //     labels: [
    //         'Red',
    //         'Yellow',
    //         'Blue'
    //     ]
    // };


    // const data = {
    //     labels: ['Počet testovaných', 'Negatívne testy', 'Pozitívne testy', 'Počet vyliečených', 'Počet úmrtií'],
    //     datasets: [{
    //         label: '# of Votes',
    //         data: [23672, 22957, 715, 23, 2],
    //         backgroundColor: [
    //             'blue',
    //             'yellow',
    //             'red',
    //             'green',
    //             'black',
    //         ],
    //     }]
    // }

    const data = {
        labels: ['Nakazení', 'Vyliečení', 'Úmrtia'],
        datasets: [{
            label: '# of Votes',
            data: [1674967, 371866, 101483],
            backgroundColor: [
                '#f85959',
                '#17b978',
                '#430d27',
            ],
        }]
    }


    return (
        <div>
            <div className="dougnut">
                <Doughnut
                    data={data}
                    height={300}
                    options={{
                        maintainAspectRatio: false,
                        animation: { duration: 2000, easing: 'easeOutQuint' }
                    }}
                />
            </div>

        </div>
    )
}

export default DougnatChart
