import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { FaSortAmountDown } from 'react-icons/fa';
import { IoIosArrowDown } from 'react-icons/io';

function CovidTable(props) {
    const [ikonka, setIkonka] = useState('cases')
    const [sipkyDole, setSipkyDole] = useState(true)
    const [sipkaKontinenty, setSipkaKontinenty] = useState({
        transition: 'all .2s ease-in',
        marginBottom: '-3px',
        marginLeft: '3px',
    })
    const [styleForRollDown, setStyleForRollDown] = useState({
        position: 'absolute',
        left: '-1000px',
        transition: 'all .2s ease-in',
        fontSize: '15px',
    })

    function zmenIkonku(key) {
        if (key === ikonka) {
            setIkonka(key + "2")
        }
        else { setIkonka(key) }
    }

    const myStyle2 = {
        transform: "rotateX(180deg)",
    }


    function myFunction() {
        var input, filter, table, tr, td, i, txtValue;
        input = document.getElementById("myInput");
        filter = input.value.toUpperCase();
        table = document.getElementById("myTable");
        tr = table.getElementsByTagName("tr");
        for (i = 0; i < tr.length; i++) {
            td = tr[i].getElementsByTagName("td")[0];
            if (td) {
                txtValue = td.textContent || td.innerText;
                if (txtValue.toUpperCase().indexOf(filter) > -1) {
                    tr[i].style.display = "";
                } else {
                    tr[i].style.display = "none";
                }
            }
        }
    }

    function FormatNum(num) {
        if (num) {
            return (
                num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")
            )
        }
    }




    function rollDownContintinents() {
        setSipkyDole(!sipkyDole)
        if (sipkyDole) {
            setSipkaKontinenty({
                transform: "rotate(180deg)",
                transition: 'all .2s ease-in',
                marginBottom: '-3px',
                marginLeft: '3px',
            })
            setStyleForRollDown({
                background: 'rgb(234, 234, 234)',
                fontSize: '15px',
                transition: 'all .2s ease-in',
            })
        }
        else {
            setStyleForRollDown({
                position: 'absolute',
                left: '-1000px',
                transition: 'all .2s ease-in',
                fontSize: '15px',
            })

            setSipkaKontinenty({
                transform: "rotate(0deg)",
                transition: 'all .2s ease-in',
                marginBottom: '-3px',
                marginLeft: '3px',
            })
        }

    }

    function functionForWorld(item, id) {
        return (<>
            <tr key={id} style={{ background: 'rgb(234, 234, 234)' }} >
                <td style={{ width: '100px' }} onClick={rollDownContintinents}>
                    Celý svet <span ><IoIosArrowDown style={sipkaKontinenty} /></span>
                </td>
                <td>{FormatNum(item.cases)}</td>
                <td>{FormatNum(item.active)}</td>
                <td style={{ background: ' rgb(254, 255, 194, .4)' }}>+{FormatNum(item.todayCases)} </td>
                <td>{FormatNum(item.deaths)}</td>
                <td style={{ color: ' rgba(156, 20, 20, 0.849)', fontWeight: 'bold' }}>+{FormatNum(item.todayDeaths)} </td>
                <td style={{ fontWeight: 'bold', color: 'green' }}>{FormatNum(item.recovered)}</td>
            </tr>
            {props.countriesData.map((item, id) => (
                item.country === 'Europe'
                    || item.country === 'North America'
                    || item.country === 'Asia'
                    || item.country === 'Africa'
                    || item.country === 'South America'
                    || item.country === 'Oceania' ?
                    <tr key={id} className="kontinenty" style={styleForRollDown}  >
                        <td style={{ width: '100px' }} className="kontinenty">{item.country} </td>
                        <td className="kontinenty">{FormatNum(item.cases)}</td>
                        <td className="kontinenty">{FormatNum(item.active)}</td>
                        <td style={{ background: ' rgb(254, 255, 194, .4)' }} className="kontinenty">+{FormatNum(item.todayCases)} </td>
                        <td className="kontinenty">{FormatNum(item.deaths)}</td>
                        <td style={{ color: ' rgba(156, 20, 20, 0.849)', fontWeight: 'bold' }} className="kontinenty">+{FormatNum(item.todayDeaths)} </td>
                        <td style={{ fontWeight: 'bold', color: 'green' }} className="kontinenty">{FormatNum(item.recovered)}</td>
                    </tr>
                    : null
            ))}

        </>
        )
    }
    return (
        <>
            <input type="text" id="myInput" onKeyUp={myFunction} placeholder="Hľadať.." className="inputSearch" />
            <div className="divTable" >
                <table id="myTable">
                    <thead>
                        <tr>
                            <th onClick={() => (props.sortBy('totalTests'), zmenIkonku('totalTests'))} >
                                Krajina
                            {ikonka === 'totalTests'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'totalTests2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('cases'), zmenIkonku('cases'))} >
                                Počet nakazených
                            {ikonka === 'cases'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'cases2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('active'), zmenIkonku('active'))} >
                                Aktívne prípady
                            {ikonka === 'active'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'active2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('todayCases'), zmenIkonku('todayCases'))} >
                                Nové prípady
                            {ikonka === 'todayCases'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'todayCases2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('deaths'), zmenIkonku('deaths'))} >
                                Počet úmrtí
                            {ikonka === 'deaths'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'deaths2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('todayDeaths'), zmenIkonku('todayDeaths'))} >
                                Nové úmrtia
                            {ikonka === 'todayDeaths'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'todayDeaths2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                            <th onClick={() => (props.sortBy('recovered'), zmenIkonku('recovered'))} >
                                Počet vyliečených
                            {ikonka === 'recovered'
                                    ? <FaSortAmountDown className="sortIcon" />
                                    : null
                                }
                                {ikonka === 'recovered2'
                                    ? <FaSortAmountDown style={myStyle2} className="sortIcon" />
                                    : null
                                }
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.countriesData.map((item, id) => (
                            item.country === 'World' ? functionForWorld(item, id) :
                                item.country === 'Europe' ? null :
                                    item.country === 'North America' ? null :
                                        item.country === 'Asia' ? null :
                                            item.country === 'Africa' ? null :
                                                item.country === 'South America' ? null :
                                                    item.country === 'Oceania' ? null :
                                                        item.country === 'Total:' ? null :
                                                            <tr key={id} >
                                                                <td style={{ width: '100px' }}>
                                                                    {item.country === 'Slovakia' ?
                                                                        <div style={{ fontWeight: 'bold', textDecoration: 'underline' }}>
                                                                            <Link to='/Covid/CovidSK'>{item.country}</Link>
                                                                        </div>
                                                                        : item.country
                                                                    }
                                                                    {item.totalTests > 0 ?
                                                                        <div className="firstCase">Počet testov: <div className="firstCaseNumber">{FormatNum(item.totalTests)}</div></div>
                                                                        : null}
                                                                </td>
                                                                <td>{FormatNum(item.cases)}</td>
                                                                <td>{FormatNum(item.active)}</td>
                                                                {item.todayCases > 0 ?
                                                                    <td style={{ background: ' rgb(254, 255, 194)' }}>+{FormatNum(item.todayCases)} </td>
                                                                    : <td></td>
                                                                }
                                                                <td>{FormatNum(item.deaths)}</td>
                                                                {item.todayDeaths > 0 ?
                                                                    <td style={{ color: ' rgba(156, 20, 20, 0.849)', fontWeight: 'bold' }}>+{FormatNum(item.todayDeaths)} </td>
                                                                    : <td></td>
                                                                }
                                                                <td style={{ fontWeight: 'bold', color: 'green' }}>{FormatNum(item.recovered)}</td>
                                                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </>
    )
}

export default CovidTable